.toggle{
    display: flex;
    justify-content: space-between;
    border: 3px solid yellow;
    border-radius: 1rem;
    position: relative;
    padding: 2px;
    cursor: pointer;
    height: 1.65rem;
    width: 3rem;
    margin-right: 1rem;
}

.toggle>*{
    width: 1rem;
    height: 1rem;
    color: yellow;
}

.t-button{
    border-radius: 100%;
    background: yellow;
    position: absolute;
}